                                <svg className="st-anim5" viewBox="0 0 2036 1050"><g><path className="st2" d="M1689.1,877.2c-11,0-21.8,0.8-32.3,2.4c105.7,12.2,187.7,102,187.7,211c0,106.3-78.1,194.4-180.1,210c8.1,0.9,16.4,1.4,24.7,1.4c117.3,0,212.4-95.1,212.4-212.4S1806.4,877.2,1689.1,877.2z" /><path className="st2" d="M1681.7,1405.4v-24.1c77.9,0,151.1-30.3,206.1-85.4s85.4-128.2,85.4-206.1s-30.3-151.1-85.4-206.1s-128.2-85.4-206.1-85.4v-24.1c42.6,0,83.9,8.3,122.9,24.8c37.6,15.9,71.3,38.6,100.3,67.6s51.7,62.7,67.6,100.3c16.5,38.9,24.8,80.2,24.8,122.8s-8.3,83.9-24.8,122.8c-15.9,37.6-38.7,71.3-67.6,100.3s-62.7,51.7-100.3,67.6C1765.6,1397.1,1724.3,1405.4,1681.7,1405.4z" /><path className="st4" d="M1632.1,878.2c-117.3,0-212.4,95.1-212.4,212.4s95.1,212.4,212.4,212.4s212.4-95.1,212.4-212.4S1749.4,878.2,1632.1,878.2z M1632.1,1196.9c-58.7,0-106.3-47.6-106.3-106.3s47.6-106.3,106.3-106.3s106.3,47.6,106.3,106.3S1690.8,1196.9,1632.1,1196.9z" /></g></svg>
