import React from "react";
import { motion } from "framer-motion";
import { BrowserView, MobileView } from "react-device-detect";

export const PageMotion = ({ children }) => {

    return (
        <>
            <BrowserView className="m-page-cont">
                <div className="m-page-box">
                    <motion.span className="m-page-r" initial={false} animate={{ x: '100%', display: 'none' }} transition={{ duration: 1, ease: [0.7, 0, 0.3, 1] }} exit={{  x: '0%', display: 'block' }} />
                    <motion.span className="m-page-r2" initial={false} animate={{ x: '100%', display: 'none' }} transition={{ duration: .8, ease: [0.7, 0, 0.3, 1] }} exit={{  x: '0%', display: 'block' }} />
                    <motion.span className="m-page-r" initial={{ x: '0%'}} animate={{ x: '-100%', delay: .2, transitionEnd: { display: 'none' } }} transition={{ duration: .9, ease: [0.7, 0, 0.3, 1] }} exit={false} />
                    <motion.span className="m-page-r2" initial={{ x: '0%'}} animate={{ x: '-100%', delay: .2, transitionEnd: { display: 'none' } }} transition={{ duration: 1.1, ease: [0.7, 0, 0.3, 1] }} exit={false} />
                </div>
                {children}
            </BrowserView>
            <MobileView className="m-page-cont">
            <div className="m-page-box">
                    <motion.span className="m-page-r" initial={false} animate={{ x: '100%', display: 'none' }} transition={{ duration: .8, ease: [0.7, 0, 0.3, 1] }} exit={{  x: '0%', display: 'block' }} />
                    <motion.span className="m-page-r2" initial={false} animate={{ x: '100%', display: 'none' }} transition={{ duration: .6, ease: [0.7, 0, 0.3, 1] }} exit={{  x: '0%', display: 'block' }} />
                    <motion.span className="m-page-r" initial={{ x: '0%'}} animate={{ x: '-100%', delay: .1, transitionEnd: { display: 'none' } }} transition={{ duration: .9, ease: [0.7, 0, 0.3, 1] }} exit={false} />
                    <motion.span className="m-page-r2" initial={{ x: '0%'}} animate={{ x: '-100%', delay: .1, transitionEnd: { display: 'none' } }} transition={{ duration: 1.1, ease: [0.7, 0, 0.3, 1] }} exit={false} />
                </div>
                {children}
            </MobileView>
        </>
    );
}